body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
  width: 100%;
  background-color: #17181c;
  /* background-color: #504B48; */
  background-image: url(/React-Calculator/static/media/ink_splash.a2b818ec.png);
  background-position: center;
  background-repeat: no-repeat;
}

.calc-wrapper{
  width: 400px;
  height: 600px;
}

.row{
  display: -webkit-flex;
  display: flex;
  width: 100%;
}
.title{
    height: 1.5em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-weight: bolder;
    font-size: 1.4em;
    background-color: #dd6413;
    font-family: 'Bungee Inline', cursive;
    color: #ffffff;
    -webkit-flex: 1 1;
            flex: 1 1;
    outline: 1px solid #888888;
}
/*
    Raise Effect on Buttons
*/
.raise {
  --color: #ffffff;
  --hover: white;
  --clearbutton: white; }

.button-wrapper {
  height: 4em;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-weight: lighter;
  font-size: 1.4em;
  background-color: #e0e1e6;
  -webkit-flex: 1 1;
          flex: 1 1;
  color: #888888;
  outline: 1px solid #888888;
  transition: 0.25s; }
  .button-wrapper:hover, .button-wrapper:focus {
    border-color: var(--hover);
    color: #fff;
    background-color: #16b0f7;
    font-weight: bold; }

.operator {
  background-color: #fe9241;
  color: #ffffff; }

.raise:hover,
.raise:focus {
  box-shadow: 0 0.2em 0.2em -0.1em var(--hover);
  -webkit-transform: translateY(-0.1em);
          transform: translateY(-0.1em); }

.input{
    height: 4em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    font-weight: bold;
    font-size: 1.4em;
    background-color: #000000;
    color: #ffffff;
    padding: 0.5em;
    outline: 1px solid #888888;
}
.close {
  --color: #ffffff;
  --hover: white; }

.clear-btn {
  height: 4em;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-weight: lighter;
  font-size: 1.4em;
  -webkit-flex: 1 1;
          flex: 1 1;
  color: #888888;
  outline: 1px solid #888888;
  background-color: #c5c3cd;
  transition: 0.25s; }
  .clear-btn:hover, .clear-btn:focus {
    border-color: var(--hover);
    color: #fff;
    background-color: #16b0f7;
    font-weight: bold; }

.close:hover,
.close:focus {
  box-shadow: 0 0.5em 0.5em -0.4em var(--hover);
  -webkit-transform: translateY(-0.25em);
          transform: translateY(-0.25em); }

